<script>
import DownloadUtil from '@/util/DownloadUtil'

export default {
  name: 'ArtStyleView',
  metaInfo: {
    title: '在线图片风格化 - 艺术滤镜转化照片',
    meta: [
      { name: 'description', content: '将普通照片转换为艺术风格，如素描、水彩、油画风等，快速生成独特图像效果。' },
      { name: 'keywords', content: '图片风格化,艺术滤镜,照片转画,在线风格转换,图像艺术化' }
    ]
  },
  data() {
    return {

      file: null,
      imgUrl: '',
      resultUrl: '',
      style: 'pixelate',
      blockSize: 10,
      edgeStrength: 1,
      loading: false,
      progress: 0,
      progressText: '',
      error: '',
      sepiaStrength: 1,
      embossStrength: 1,
      blurRadius: 2,
      oilBlockSize: 6,
      mosaicBlockSize: 12,
      contrastLevel: 2
    }
  },
  computed: {
    styleOptions() {
      return [
        { label: '像素化风格', value: 'pixelate' },
        { label: '素描/边缘检测', value: 'sketch' },
        { label: '灰度', value: 'grayscale' },
        { label: '反色', value: 'invert' },
        { label: '怀旧', value: 'sepia' },
        { label: '浮雕', value: 'emboss' },
        { label: '模糊', value: 'blur' },
        { label: '油画', value: 'oil' },
        { label: '马赛克', value: 'mosaic' },
        { label: '高对比', value: 'highcontrast' }
      ];
    }
  },
  methods: {
    handleUploadChange(file, fileList) {
      if (fileList.length > 0) {
        this.file = fileList[fileList.length - 1].raw || fileList[fileList.length - 1];
        this.imgUrl = URL.createObjectURL(this.file);
        this.resultUrl = '';
        this.error = '';
      } else {
        this.file = null;
        this.imgUrl = '';
        this.resultUrl = '';
        this.error = '';
      }
    },
    async handleProcess() {
      if (!this.file) return;
      this.loading = true;
      this.progress = 0;
      this.progressText = '处理中...';
      this.error = '';
      try {
        let url = '';
        switch (this.style) {
          case 'pixelate':
            url = await this.pixelateImage(this.file, this.blockSize);
            break;
          case 'sketch':
            url = await this.sketchImage(this.file, this.edgeStrength);
            break;
          case 'grayscale':
            url = await this.grayscaleImage(this.file);
            break;
          case 'invert':
            url = await this.invertImage(this.file);
            break;
          case 'sepia':
            url = await this.sepiaImage(this.file, this.sepiaStrength);
            break;
          case 'emboss':
            url = await this.embossImage(this.file, this.embossStrength);
            break;
          case 'blur':
            url = await this.blurImage(this.file, this.blurRadius);
            break;
          case 'oil':
            url = await this.oilImage(this.file, this.oilBlockSize);
            break;
          case 'mosaic':
            url = await this.mosaicImage(this.file, this.mosaicBlockSize);
            break;
          case 'highcontrast':
            url = await this.highContrastImage(this.file, this.contrastLevel);
            break;
        }
        this.resultUrl = url;
      } catch (e) {
        this.error = '处理失败：' + (e.message || e);
      }
      this.loading = false;
      this.progress = 0;
      this.progressText = '';
    },
    async pixelateImage(file, blockSize) {
      const img = await this.loadImageFromFile(file);
      const w = img.width, h = img.height;
      const canvas = document.createElement('canvas');
      canvas.width = w;
      canvas.height = h;
      const ctx = canvas.getContext('2d');
      ctx.imageSmoothingEnabled = false;
      ctx.clearRect(0, 0, w, h);
      ctx.drawImage(img, 0, 0, w/blockSize, h/blockSize);
      ctx.drawImage(canvas, 0, 0, w/blockSize, h/blockSize, 0, 0, w, h);
      return canvas.toDataURL('image/png');
    },
    async sketchImage(file, edgeStrength) {
      const img = await this.loadImageFromFile(file);
      const w = img.width, h = img.height;
      const canvas = document.createElement('canvas');
      canvas.width = w;
      canvas.height = h;
      const ctx = canvas.getContext('2d');
      ctx.drawImage(img, 0, 0, w, h);
      let imageData = ctx.getImageData(0, 0, w, h);
      // 灰度
      for (let i = 0; i < imageData.data.length; i += 4) {
        const avg = (imageData.data[i] + imageData.data[i+1] + imageData.data[i+2]) / 3;
        imageData.data[i] = imageData.data[i+1] = imageData.data[i+2] = avg;
      }
      // 简单Sobel边缘检测
      const kernelX = [
        -1, 0, 1,
        -2, 0, 2,
        -1, 0, 1
      ];
      const kernelY = [
        -1, -2, -1,
         0,  0,  0,
         1,  2,  1
      ];
      const src = imageData.data;
      const dst = new Uint8ClampedArray(src.length);
      for (let y = 1; y < h-1; y++) {
        for (let x = 1; x < w-1; x++) {
          let gx = 0, gy = 0;
          for (let ky = -1; ky <= 1; ky++) {
            for (let kx = -1; kx <= 1; kx++) {
              const px = ((y+ky)*w + (x+kx)) * 4;
              const val = src[px];
              const kIdx = (ky+1)*3 + (kx+1);
              gx += kernelX[kIdx] * val;
              gy += kernelY[kIdx] * val;
            }
          }
          const mag = Math.sqrt(gx*gx + gy*gy) * edgeStrength;
          const idx = (y*w + x) * 4;
          dst[idx] = dst[idx+1] = dst[idx+2] = 255 - Math.min(255, mag);
          dst[idx+3] = 255;
        }
      }
      imageData.data.set(dst);
      ctx.putImageData(imageData, 0, 0);
      return canvas.toDataURL('image/png');
    },
    async grayscaleImage(file) {
      const img = await this.loadImageFromFile(file);
      const w = img.width, h = img.height;
      const canvas = document.createElement('canvas');
      canvas.width = w;
      canvas.height = h;
      const ctx = canvas.getContext('2d');
      ctx.drawImage(img, 0, 0, w, h);
      let imageData = ctx.getImageData(0, 0, w, h);
      for (let i = 0; i < imageData.data.length; i += 4) {
        const avg = (imageData.data[i] + imageData.data[i+1] + imageData.data[i+2]) / 3;
        imageData.data[i] = imageData.data[i+1] = imageData.data[i+2] = avg;
      }
      ctx.putImageData(imageData, 0, 0);
      return canvas.toDataURL('image/png');
    },
    async invertImage(file) {
      const img = await this.loadImageFromFile(file);
      const w = img.width, h = img.height;
      const canvas = document.createElement('canvas');
      canvas.width = w;
      canvas.height = h;
      const ctx = canvas.getContext('2d');
      ctx.drawImage(img, 0, 0, w, h);
      let imageData = ctx.getImageData(0, 0, w, h);
      for (let i = 0; i < imageData.data.length; i += 4) {
        imageData.data[i] = 255 - imageData.data[i];
        imageData.data[i+1] = 255 - imageData.data[i+1];
        imageData.data[i+2] = 255 - imageData.data[i+2];
      }
      ctx.putImageData(imageData, 0, 0);
      return canvas.toDataURL('image/png');
    },
    async sepiaImage(file, strength=1) {
      const img = await this.loadImageFromFile(file);
      const w = img.width, h = img.height;
      const canvas = document.createElement('canvas');
      canvas.width = w;
      canvas.height = h;
      const ctx = canvas.getContext('2d');
      ctx.drawImage(img, 0, 0, w, h);
      let imageData = ctx.getImageData(0, 0, w, h);
      for (let i = 0; i < imageData.data.length; i += 4) {
        const r = imageData.data[i], g = imageData.data[i+1], b = imageData.data[i+2];
        imageData.data[i] = Math.min(255, (r * (1 - 0.607 * strength) + g * 0.769 * strength + b * 0.189 * strength));
        imageData.data[i+1] = Math.min(255, (r * 0.349 * strength + g * (1 - 0.314 * strength) + b * 0.168 * strength));
        imageData.data[i+2] = Math.min(255, (r * 0.272 * strength + g * 0.534 * strength + b * (1 - 0.869 * strength)));
      }
      ctx.putImageData(imageData, 0, 0);
      return canvas.toDataURL('image/png');
    },
    async embossImage(file, strength=1) {
      const img = await this.loadImageFromFile(file);
      const w = img.width, h = img.height;
      const canvas = document.createElement('canvas');
      canvas.width = w;
      canvas.height = h;
      const ctx = canvas.getContext('2d');
      ctx.drawImage(img, 0, 0, w, h);
      let imageData = ctx.getImageData(0, 0, w, h);
      const data = imageData.data;
      for (let y = 1; y < h; y++) {
        for (let x = 1; x < w; x++) {
          const idx = (y * w + x) * 4;
          const idx2 = ((y-1) * w + (x-1)) * 4;
          for (let c = 0; c < 3; c++) {
            const val = data[idx + c] - data[idx2 + c] + 128;
            data[idx + c] = Math.max(0, Math.min(255, val * strength));
          }
        }
      }
      ctx.putImageData(imageData, 0, 0);
      return canvas.toDataURL('image/png');
    },
    async blurImage(file, radius=2) {
      const img = await this.loadImageFromFile(file);
      const w = img.width, h = img.height;
      const canvas = document.createElement('canvas');
      canvas.width = w;
      canvas.height = h;
      const ctx = canvas.getContext('2d');
      ctx.filter = `blur(${radius}px)`;
      ctx.drawImage(img, 0, 0, w, h);
      ctx.filter = 'none';
      return canvas.toDataURL('image/png');
    },
    async oilImage(file, blockSize=6) {
      // 简易油画风格：每块取众数色
      const img = await this.loadImageFromFile(file);
      const w = img.width, h = img.height;
      const canvas = document.createElement('canvas');
      canvas.width = w;
      canvas.height = h;
      const ctx = canvas.getContext('2d');
      ctx.drawImage(img, 0, 0, w, h);
      let imageData = ctx.getImageData(0, 0, w, h);
      const data = imageData.data;
      for (let y = 0; y < h; y += blockSize) {
        for (let x = 0; x < w; x += blockSize) {
          const colorMap = {};
          let maxCount = 0, mainColor = [0,0,0];
          for (let dy = 0; dy < blockSize; dy++) {
            for (let dx = 0; dx < blockSize; dx++) {
              const px = ((y+dy)*w + (x+dx)) * 4;
              if (px >= data.length) continue;
              const key = `${data[px]},${data[px+1]},${data[px+2]}`;
              colorMap[key] = (colorMap[key]||0)+1;
              if (colorMap[key] > maxCount) {
                maxCount = colorMap[key];
                mainColor = [data[px],data[px+1],data[px+2]];
              }
            }
          }
          for (let dy = 0; dy < blockSize; dy++) {
            for (let dx = 0; dx < blockSize; dx++) {
              const px = ((y+dy)*w + (x+dx)) * 4;
              if (px >= data.length) continue;
              data[px] = mainColor[0];
              data[px+1] = mainColor[1];
              data[px+2] = mainColor[2];
            }
          }
        }
      }
      ctx.putImageData(imageData, 0, 0);
      return canvas.toDataURL('image/png');
    },
    async mosaicImage(file, blockSize=12) {
      // 类似像素化，但块更大
      const img = await this.loadImageFromFile(file);
      const w = img.width, h = img.height;
      const canvas = document.createElement('canvas');
      canvas.width = w;
      canvas.height = h;
      const ctx = canvas.getContext('2d');
      ctx.drawImage(img, 0, 0, w, h);
      let imageData = ctx.getImageData(0, 0, w, h);
      const data = imageData.data;
      for (let y = 0; y < h; y += blockSize) {
        for (let x = 0; x < w; x += blockSize) {
          let r=0,g=0,b=0,cnt=0;
          for (let dy = 0; dy < blockSize; dy++) {
            for (let dx = 0; dx < blockSize; dx++) {
              const px = ((y+dy)*w + (x+dx)) * 4;
              if (px >= data.length) continue;
              r += data[px];
              g += data[px+1];
              b += data[px+2];
              cnt++;
            }
          }
          r = Math.round(r/cnt);
          g = Math.round(g/cnt);
          b = Math.round(b/cnt);
          for (let dy = 0; dy < blockSize; dy++) {
            for (let dx = 0; dx < blockSize; dx++) {
              const px = ((y+dy)*w + (x+dx)) * 4;
              if (px >= data.length) continue;
              data[px] = r;
              data[px+1] = g;
              data[px+2] = b;
            }
          }
        }
      }
      ctx.putImageData(imageData, 0, 0);
      return canvas.toDataURL('image/png');
    },
    async highContrastImage(file, level=2) {
      // level>1 提高对比度
      const img = await this.loadImageFromFile(file);
      const w = img.width, h = img.height;
      const canvas = document.createElement('canvas');
      canvas.width = w;
      canvas.height = h;
      const ctx = canvas.getContext('2d');
      ctx.drawImage(img, 0, 0, w, h);
      let imageData = ctx.getImageData(0, 0, w, h);
      const factor = (259 * (level + 255)) / (255 * (259 - level));
      for (let i = 0; i < imageData.data.length; i += 4) {
        imageData.data[i] = Math.max(0, Math.min(255, factor * (imageData.data[i] - 128) + 128));
        imageData.data[i+1] = Math.max(0, Math.min(255, factor * (imageData.data[i+1] - 128) + 128));
        imageData.data[i+2] = Math.max(0, Math.min(255, factor * (imageData.data[i+2] - 128) + 128));
      }
      ctx.putImageData(imageData, 0, 0);
      return canvas.toDataURL('image/png');
    },
    loadImageFromFile(file) {
      return new Promise((resolve, reject) => {
        const img = new window.Image();
        img.onload = () => resolve(img);
        img.onerror = reject;
        img.src = URL.createObjectURL(file);
      });
    },
    handleDownload() {
      if (!this.resultUrl) return;
      const filename = this.style === 'pixelate' ? 'pixelated.png' : 'sketch.png';
      DownloadUtil.downloadImage(this.resultUrl, filename);
    }
  },
  beforeDestroy() {
    if (this.imgUrl) URL.revokeObjectURL(this.imgUrl);
    this.imgUrl = '';
    this.resultUrl = '';
  }
}
</script>
<template>
  <div class="modern-bg">
    <el-row :gutter="0" justify="center">
      <el-col :xs="{span: 24, offset: 0}" :sm="{span: 22, offset: 1}" :md="{span: 18, offset: 3}" :lg="{span: 14, offset: 5}" :xl="{span: 12, offset: 6}">
        <el-card shadow="hover" class="modern-card">
          <div class="upload-row">
            <el-upload
              action="#"
              :auto-upload="false"
              :multiple="false"
              :show-file-list="false"
              accept="image/*"
              :on-change="handleUploadChange"
              :disabled="loading"
            >
              <el-button type="primary" round :disabled="loading">选择图片</el-button>
            </el-upload>
            <el-select v-model="style" style="width: 160px;" :disabled="loading" filterable>
              <el-option v-for="opt in styleOptions" :key="opt.value" :label="opt.label" :value="opt.value" />
            </el-select>
            <el-slider v-if="style==='pixelate'" v-model="blockSize" :min="2" :max="50" :step="1" style="width:140px;" :disabled="loading" />
            <el-slider v-if="style==='sketch'" v-model="edgeStrength" :min="0.5" :max="3" :step="0.1" style="width:140px;" :disabled="loading" />
            <el-slider v-if="style==='sepia'" v-model="sepiaStrength" :min="0.1" :max="1" :step="0.05" style="width:120px;" :disabled="loading" />
            <el-slider v-if="style==='emboss'" v-model="embossStrength" :min="0.5" :max="2" :step="0.05" style="width:120px;" :disabled="loading" />
            <el-slider v-if="style==='blur'" v-model="blurRadius" :min="1" :max="10" :step="1" style="width:120px;" :disabled="loading" />
            <el-slider v-if="style==='oil'" v-model="oilBlockSize" :min="2" :max="20" :step="1" style="width:120px;" :disabled="loading" />
            <el-slider v-if="style==='mosaic'" v-model="mosaicBlockSize" :min="4" :max="40" :step="1" style="width:120px;" :disabled="loading" />
            <el-slider v-if="style==='highcontrast'" v-model="contrastLevel" :min="1" :max="10" :step="0.1" style="width:120px;" :disabled="loading" />
            <el-button type="success" round :disabled="!file || loading" @click="handleProcess">一键生成</el-button>
            <el-button v-if="resultUrl" type="primary" size="mini" @click="handleDownload" :disabled="loading">下载图片</el-button>
          </div>
          <el-progress v-if="loading || progress > 0" :percentage="progress" status="success" :stroke-width="16" text-inside style="margin: 16px 0;" />
          <div v-if="error" style="color: #f56c6c; margin: 12px 0; text-align: center;">{{error}}</div>
          <el-row :gutter="16" style="margin-top: 24px;">
            <el-col :span="12">
              <div style="text-align:center;">
                <div style="font-weight:600; margin-bottom:8px;">原图预览</div>
                <el-image v-if="imgUrl" :src="imgUrl" :preview-src-list="loading ? [] : [imgUrl,resultUrl]" style="max-width:100%;max-height:320px;border-radius:10px;box-shadow:0 2px 8px rgba(0,0,0,0.08);" fit="contain" />
                <div v-else style="color:#ccc;">请上传图片</div>
              </div>
            </el-col>
            <el-col :span="12" style="position:relative;">
              <div style="text-align:center;">
                <div style="font-weight:600; margin-bottom:8px;">风格化预览</div>
                <el-image
                  v-if="resultUrl"
                  :src="resultUrl"
                  style="max-width:100%;border-radius:10px;box-shadow:0 2px 8px rgba(0,0,0,0.08);background:#f0f9eb;"
                  fit="contain"
                  :preview-src-list="loading ? [] : [resultUrl,imgUrl]"
                />
              </div>
            </el-col>
          </el-row>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>
<style scoped>
.el-image{
    max-height: 100% !important;
}
.modern-bg {
  //min-height: 100vh;
  background: #f6f8fa;
  //padding: 32px 0;
}
.modern-card {
  border-radius: 16px;
  box-shadow: 0 4px 24px rgba(0,0,0,0.08);
  margin-bottom: 32px;
}
.upload-row {
  display: flex;
  align-items: center;
  gap: 16px;
  justify-content: center;
}

@media screen and (max-width: 768px) {
  .upload-row {
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .modern-card >>> .el-card__body{
    padding: 5px;
  }
  ::v-deep .el-table .el-table__cell{
    padding: 5px 0;
  }
  ::v-deep .el-table th.el-table__cell>.cell {
    padding-left: 5px!important;
    padding-right: 5px!important;
  }
}




</style> 